"use client";

import React, { useState, useEffect, ChangeEvent } from "react";
import axios from "axios";

interface Stock {
  symbol: string;
  name: string;
  changePercent?: number;
}

export default function Home() {
  const [topPerformers, setTopPerformers] = useState<Stock[]>([]);
  const [searchTerm, setSearchTerm] = useState<string>("");
  const [searchResults, setSearchResults] = useState<Stock[]>([]);

  // Fetch top performers on mount
  useEffect(() => {
    async function fetchTopPerformers() {
      try {
        const response = await axios.get("http://localhost:5000/api/top-performers");
        setTopPerformers(response.data);
      } catch (error) {
        console.error("Error fetching top performers:", error);
      }
    }
    fetchTopPerformers();
  }, []);

  // Handle search input change
  const handleSearchChange = async (e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setSearchTerm(value);
    if (value.length > 2) {
      try {
        const response = await axios.get(`http://localhost:5000/api/search?query=${value}`);
        setSearchResults(response.data);
      } catch (error) {
        console.error("Error searching stocks:", error);
      }
    } else {
      setSearchResults([]);
    }
  };

  return (
    <main className="min-h-screen bg-black text-white p-6 font-sans">
      <h1 className="text-4xl font-bold mb-6">Stock Market Dashboard</h1>
      <div className="mb-6">
        <input
          type="text"
          placeholder="Search stocks by keyword or symbol"
          value={searchTerm}
          onChange={handleSearchChange}
          className="w-full p-3 rounded bg-gray-900 text-white border border-gray-700 focus:outline-none focus:ring-2 focus:ring-white"
        />
        {searchResults.length > 0 && (
          <ul className="bg-gray-900 mt-2 rounded max-h-60 overflow-auto">
            {searchResults.map((stock) => (
              <li key={stock.symbol} className="p-2 border-b border-gray-700">
                {stock.symbol} - {stock.name}
              </li>
            ))}
          </ul>
        )}
      </div>
      <section>
        <h2 className="text-2xl font-semibold mb-4">Top Performers</h2>
        <ul>
          {topPerformers.map((stock) => (
            <li key={stock.symbol} className="mb-2">
              {stock.symbol} - {stock.name} ({stock.changePercent ?? 0}%)
            </li>
          ))}
        </ul>
      </section>
    </main>
  );
}
